.MainTable {
    border-width: 0;
    border-style: solid;
    border-collapse: collapse;
    margin: 0.2em;
    border-spacing: 0;
    width: 1%;
}

.MainTable > thead > tr > th {
    padding: 0.25em;
    border-width: 1px;
    border-style: solid;
    vertical-align: center;
    font-weight: bold;
    width: 1%;
}

.MainTableSpan {
    vertical-align: center;
    background-color: white;
}

.MainTable > tbody > tr > th {
    padding: 0.25em;
    border-width: 1px;
    border-style: solid;
}

.MainTable > tbody > tr > td {
    padding: 0.25em;
    border-width: 1px;
    border-style: solid;
}

.MainTableEpic {
    background-color: #bfffdf;
}

.MainTableBusiness {
    background-color: #eaffbf;
}

.MainTableBusinessActive {
    background-color: #ace639;
    white-space: nowrap;
    position: relative;
    padding-left: 1.2em !important;
}

.MainTableDev {
    background-color: #bfe0ff;
}

.MainTableDevHeader {
    background-color: #bfe0ff;
    white-space: nowrap;
}

.MainTableDevActive {
    background-color: #80c1ff;
    white-space: nowrap;
    position: relative;
    padding-left: 1.2em !important;
}

th.MainTableSpace {
    height: 0.2em;
}

.MainTableHeaderTop1 {
    position: sticky;
    top: 0;
    height: 4em;
    z-index: 1;
}

.MainTableHeaderTop2 {
    position: sticky;
    top: 4.6em;
    height: 4em;
    z-index: 1;
}

.MainTableHeaderTop1Left1 {
    position: sticky;
    top: 0;
    left: 0;
    height: 4em;
    width: 10em;
    z-index: 2;
    background-color: white;
}

.MainTableHeaderTop2Left1 {
    position: sticky;
    top: 4.6em;
    left: 0;
    height: 4em;
    width: 8em;
    z-index: 2;
    background-color: white;
}

.MainTableHeaderTop2Left2 {
    position: sticky;
    top: 4.6em;
    left: 8em;
    height: 4em;
    width: 8em;
    z-index: 2;
    background-color: white;
}

.MainTableHeaderTop2Left3 {
    position: sticky;
    top: 4.6em;
    left: 16em;
    height: 4em;
    width: 8em;
    z-index: 2;
    background-color: white;
}

.MainTableHeaderLeft1 {
    position: sticky;
    left: 0;
    width: 8em;
    z-index: 1;
}

.MainTableHeaderLeft2 {
    position: sticky;
    left: 8em;
    width: 8em;
    z-index: 1;
}

.MainTableHeaderLeft3 {
    position: sticky;
    left: 16em;
    width: 8em;
    z-index: 1;
}

.MainTableBG {
    background-color: white;
}

.MainTableCounterHolder {
    position: relative;
}

.MainTableCounter {
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 0.2em;
    padding: 0.2em;
    background-color: dodgerblue;
    border-radius: 0.5em;
    color: white;
    font-weight: bold;
    font-size: 75%;
}

.MainTablePriority {
    position: absolute;
    left: 0;
    top: 0;
    width: 1em;
    height: 1em;
    padding-left: 0.2em;
    padding-right: 0.2em;
}

.MainTableIssueTooltipPlaceholder .MainTableIssueTooltip {
    background-color: white;
    color: black;
    border-radius: 0.5em;
    border-width: 1px;
    border-style: solid;
    border-color: #2c3e50;
    z-index: 100;
}

.IssueTooltipKey {
    font-weight: bold;
}

.IssueTooltipSummary {
    font-weight: bold;
}

.IssueTooltipPriority {
    text-align: right;
}

.IssueTooltipPriorityValue {
    font-weight: bold;
    text-align: left;
}

.IssueTooltipStatus {
    text-align: right;
}

.IssueTooltipStatusValue {
    font-weight: bold;
    text-align: left;
}

.IssueTooltipStatusTime {
    font-weight: normal;
}

.IssueTooltipAssignee {
    text-align: right;
}

.IssueTooltipAssigneeValue {
    font-weight: bold;
    text-align: left;
}

.IssueTooltipBlocks {
    text-align: right;
}

.IssueTooltipBlocksValue {
    font-weight: normal;
    text-align: left;
}

.MainTableBlockedIssue {
    background-color: rgba(255, 64, 64, 0.75) !important;
}